import isEmpty from "./isEmpty"


const validateAccountCreation = (accountData) => {
    const errors = {}
    if (isEmpty(accountData.name)) {
        errors.name = 'Provide name of account!'
    }
    if (isEmpty(accountData.type)) {
        errors.type = 'Provide type of account!'
    }
    if (isEmpty(accountData.openingBalance)) {
        errors.openingBalance = 'Provide opening balance of account!'
    }
    if (isEmpty(accountData.openingDate.toISOString())) {
        errors.openingDate = 'Provide opening date of account!'
    }


    if (!isEmpty(accountData.linkedWith) && isEmpty(accountData.partyId)) {
        errors.partyId = `Select the ${accountData.linkedWith}!`
    }



    if (accountData.hasParent && isEmpty(accountData.parentId)) {
        errors.parentId = 'Parent must be selected!'
    }

    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }
}



const validateAccountTypeCreation = (accountTypeData) => {
    const errors = {}
    if (isEmpty(accountTypeData.name)) {
        errors.name = 'Provide name of account type!'
    }
    if (isEmpty(accountTypeData.header)) {
        errors.header = 'Provide header of the account type!'
    }
    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }
}

export { validateAccountCreation, validateAccountTypeCreation }